<div class="fluctuating-container"><slot name="prefix"></slot><div class="data-container"><a-digital-transform :value="value" :useGrouping="seperator" :interval="interval"></a-digital-transform><span class="unit-box">{{ unit }}</span><transition name="lotus"><p class="fluctuating-animate" v-show="isShow" :style="{color: `${changeValue &gt; 0 ? plusColor : reduceColor}`}">{{ changeValue }}</p></transition></div></div>